/* Adjust the margin for watering history */
.watering-history {
  margin-top: 30px;
  margin-left: 400px; /* Adjust the margin as needed */
}

/* Style for watered dates */
.watered {
  background: red; /* Background color for watered dates */
  color: white; /* Text color for watered dates */
}

/* Center the pots horizontally */
.pot-table {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; /* To align pots in rows, change justify-content to flex-start */
}

/* Adjust the spacing between pot cells */
.pot-cell {
  margin: 5px;
  min-width: 50px; /* Adjust the minimum width of each pot cell */
  background-color: #ccc;
  border: 1px solid #ccc;
  padding: 10px;
}

/* Adjust the spacing between the checkbox and label */
.pot-label {
  margin-top: 5px;
}

/* Margin for action buttons */
.action-button {
  margin-right: 10px;
}